@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


* {
  font-family: 'Roboto', sans-serif;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* Header */

header {
  justify-content: space-between;
  align-items: center;
  padding: 15px 3%;
  background-color: rgb(255, 0, 51);

  /* Make it a flex-item */
  display: flex;

  /* Make header fixed */
  position: fixed;
  top: 0;
  width: 100vw;
  height: 5vh;
}

  .logo-text {
    font-size: 1.2em;
    font-weight: bold;
    color: white;
  }

  .search {
    display: inline-block;
    width: 60%
  }

    .search-form {
      display: inline-block;
      width: 100%;
      text-align: center;
    }

      .search-input {
        width: 30%;
        border: 1px solid white;
        border-radius: 4px;
        background-color: rgb(255, 0, 51);
        padding: 0.5em 0 0.5em 0;
        transition: width 0.4s ease-in-out;
        padding: 5px;

        font-size: 12px;
        color: white;
      }

        .search-input:focus {
          width: 90%;
          outline-width: 0;
        }

        .search-button {
          display: inline-block;
          justify-content: center;
          color: white;
          border: 1px solid white;
          border-radius: 4px;
          background-color: rgb(255, 0, 51);
          margin-left: 10px;
          padding: 0.36em;
        }

          .search-icon {
            width: 1em;
            height: 0.9em;
          }

        ::-webkit-input-placeholder { /* Edge */
          color: white;
        }

        :-ms-input-placeholder {
          color: white;
        }

        ::-ms-input-placeholder {
          color: white;
        }

        ::placeholder {
          color: white;
        }


  .menu-container {
      display: flex;
    }

    .live-button {
      display: inline-block;
      justify-content: center;
      color: white;
      border: 1px solid white;
      border-radius: 4px;
      background-color: rgb(255, 0, 51);
      margin-right: 20px;
      padding: 0.36em;
    }

    .profile-icon {
      display: inline-block;
      width: 1.5em;
      height: 1.5em;
      border-radius: 50%;
    }



/* Container w/ all content */

.content {
  margin-top: 5vh;
}

  /* Homepage & broadcast tiles */

  .homepage {
    background-color: rgb(245, 245, 245);
    /* To evenly space broadcast tiles */
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 2%;
  }

    .broadcast-tile {
      width: 32%;
      padding-bottom: 6%;
      margin-bottom: 2%;
    }

      .broadcast-thumb {
        width: 100%;
        height: 100%;
        padding-top: 56.25%;
      }

    .broadcast-details {
      display: flex;
      flex-wrap: wrap;
      margin-top: 3%;
    }

      .broadcast-profilepic img {
        width: 2.5rem;
        height: 2.5rem;
        border-radius: 50%;
      }

      .broadcast-title-owner {
        margin-left: 3%;
      }

      .broadcast-link {
        display: block;
      }







/* Broadcast page */

  .broadcastFormContainer {
    margin-top: 7vh;
    margin-left: 2vw;
  }

  .broadcast {
    height: 95vh;
    display: flex;
    flex-wrap: wrap;
    background-color: rgb(168, 168, 168);
  }

    .videoplayer {
      width: 80vw;
      background-color: rgb(104, 88, 192);
    }

    .chat {
      width: 20vw;
      background-color: rgb(110, 160, 132);
    }

      /* #chatList {

      } */

      #chatForm {
        position: fixed;
        bottom: 0;
      }

      /* #chatInput {

      }

      #chatButton {

      } */



@media (max-width: 600px){

    .videoplayer {
      width: 100vw;
    }

    .chat {
      width: 100vw;
    }

}




/* Style of menu items (links) */
.menu-item {
  color: #1a1a1a;
  text-decoration: none;
  padding: 10px;
}

.menu-item img {
  width: 1.55em;
  padding-right: 0.8em;
}


/* Position and sizing of burger button */
.bm-burger-button {
  position: relative;
  width: 2em;
  height: 1.4em;
  left: 1em;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: #FFFFFF;
}

/* Color/shape of burger icon bars on hover*/
.bm-burger-bars-hover {
  background: #a90000;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #1a1a1a;
}

/*
Sidebar wrapper styles
Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
*/
.bm-menu-wrap {
  position: fixed;
  height: 100%;
  top: 0
}

/* General sidebar styles */
.bm-menu {
  background: #f0f0f0;
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
  padding: 0.8em;
}

/* Individual item */
.bm-item {
  display: inline-block;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}
